* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 
}

a ,li ,ul,ol{
  margin: 0px;
  padding: 0px;
  text-decoration: none;
  outline: none;
  list-style: none;
  margin-bottom: 0rem;
  color: white;
  
}


body {
  font-family: 'Roboto', sans-serif;
}

:root{
  --primary:#212121 ;
  --sombra: 0 .5rem 1rem rgba(0,0,0,.15);
}
.navbar {
  background-color: var(--primary);
}
.hero {
  height: 90vh;
   background-image: linear-gradient(rgba(16, 16, 16, .4), rgba(16, 16, 16, .5)) , url(./assets/IMG.jpg);
  background-size: cover;
  background-position: center;
  width: 100%;
  margin-left: 0px !important;
  background-color: #212121;
  position: relative;
}
.hero h3 {
  font-family: 'Montserrat', sans-serif;
  color: white;
}
.hero  h1 {
  font-weight: 900;
  font-size: 5em;
  color: var(--primary);
}

.hero p {
  font-family: 'Montserrat', sans-serif;
  color: white;
}
.about{
  margin-top:50px;
  margin-bottom:150px;
   
  width: 100%;
}
.about h2 {
  text-align: center;
  align-items: center;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: .3em;
  color:gray;
}

.about .divider, .products .divider {
  background-color: gray;
  width: 50px;
  height: 3px;
  margin: 0px auto;
}
.about .about-info {
 
  background-color: white;
  border-radius: 32px;
  padding: 15px;
  box-shadow: var(--sombra);

}
.p{
  text-align: center;
  color: black;
  font-family: 25px;
  margin:10px;
  padding: 10px;
}


.footer{background-color:#696969;color:#fff;padding:30px;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}